import React, { FormEvent } from "react";
import { Link, useNavigate } from "react-router-dom";
import { Button, EmailInput } from "@ya.praktikum/react-developer-burger-ui-components";
import { forgotPasswordRequestAsync } from "../../services/actions/forgot-password";
import { getCookie } from "../../components/util/cookie";
import styles from "./ForgotPassword.module.css";
import Preloader from "../../components/preloader/preloader";
import { useForm } from "../../hooks/useForm";
import { useDispatch } from "../../hooks/useDispatch";
import { useSelector } from "../../hooks/useSelector";

type TForgotPassword = (e: FormEvent<HTMLFormElement>) => void;

export const ForgotPasswordPage = () => {
  const { values, handleChange } = useForm({ email: "" });
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const forgotAnswer = useSelector((state) => state.forgotPassword.success);
  const token = getCookie("accessToken");

  const forgotPassword = React.useCallback<TForgotPassword>(
    (e) => {
      e.preventDefault();
      dispatch(forgotPasswordRequestAsync(values));
    },
    [dispatch, values],
  );

  React.useEffect(() => {
    if (forgotAnswer) {
      navigate("/reset-password", { replace: true });
    }

    if (token) {
      navigate("/", { replace: true });
    }
  }, [forgotAnswer, navigate, token]);

  if (token) {
    return <Preloader />;
  }

  return (
    <section className={styles.ForgotPasswordPage}>
      <form onSubmit={forgotPassword} className={styles.Form}>
        <h1 className="mb-6"> </h1>

        <div className="mb-6">
          <EmailInput
            onChange={handleChange}
            placeholder=" e-mail"
            value={values.email}
            name={"email"}
            isIcon={false}
          />
        </div>

        <Button htmlType="submit" type="primary" size="medium">
          
        </Button>
      </form>
      <div className={`mt-20 ${styles.ForgotPasswordPageFooter}`}>
        <span className="text_color_inactive"> ?</span>
        <Link to="/login" className={styles.Login}>
          
        </Link>
      </div>
    </section>
  );
};
